import { Component, OnInit } from '@angular/core';
import { distanceInWords } from 'date-fns';

@Component({
  selector: 'app-chat',
  templateUrl: './chat.component.html',
  styleUrls: ['./chat.component.css']
})
export class ChatComponent implements OnInit {

  socket: WebSocket;
  msgs: Msg[] = [];
  id;
  message;

  constructor() { }

  ngOnInit() {
    this.id = this.guid();
    console.log(this.id);
    this.socket = new WebSocket("ws://1.1.10.135:8080/chatRoom/websocket?id="+this.id);
    this.openWebSocket();
  }

  openWebSocket(){
    try {
      this.socket.onopen = () => {
        this.doSocketOpen(this.socket.readyState);

      }
      this.socket.onmessage = (msg: any) => {
        this.doSocketMessage(msg);
      }
      this.socket.onclose = (event: CloseEvent) => {
        this.doSocketClose(event);
      }
    } catch (ex) {
      console.log(ex);
    }
  }

  doSocketOpen(readyState: number) {
    if (readyState != 1)
      return;
    this.socket.send('websocket');
  }

  doSocketMessage(message){
    console.log(message)
    let msg = JSON.parse(message.data);
    console.log(msg);
    this.msgs.push(msg);
    this.bottom();
  }

  doSocketClose(event: CloseEvent) {
    console.log("WebSocket连线中断！");
  }

  send(){
    if(this.message==''){
      return;
    }
    this.socket.send(this.message);
    this.message = '';
  }

  S4() {
    return (((1+Math.random())*0x10000)|0).toString(16).substring(1);
  }

  guid() {
    return (this.S4()+this.S4()+"-"+this.S4()+"-"+this.S4()+"-"+this.S4()+"-"+this.S4()+this.S4()+this.S4());
  }

  bottom(){
    // console.log(document.getElementById('bottom').offsetTop);
    // let top = document.getElementById('scrollBody').scrollTop = document.getElementById('bottom').offsetTop+50;
    // console.log(top);
    document.getElementById('bottom').scrollIntoView();
  }


}

export class Msg{
  id;
  messsge;
}
